
class FeaturesController {
    static retrieveFeaturesInformation = async () => {
        const response = await fetch('https://sweekwang.github.io/golabel/overall.json');
        const data = await response.json();
        const node = data.elements.nodes;
        node.sort((a,b) => a.data.shared_name.toLowerCase() > b.data.shared_name.toLowerCase() ? 1 : -1);

        return new Promise((resolve, rejected) => {
            resolve(data.elements.nodes)
        })
    };
}

export default FeaturesController;